import React from 'react';
import PropTypes from 'prop-types';
import bridge from '@vkontakte/vk-bridge';
import { PanelHeader, Header, Div, Group, Button, Panel } from '@vkontakte/vkui';
import Chart from '../components/charts/Chart';

import './Home.css';

import Api from '../core/api/Api';

// Redux
import { connect } from 'react-redux';
import { openRegionsList } from '../redux/actions/region';

class Home extends React.Component {

	constructor(props) {
    super(props);
    this.state = {};
  };

	// Открыть список областей или стран
  openRegionsList = (world) => {
    this.props.onOpenRegionsList(world).then(() => this.props.navigate('regions-list'));
  };

	render() {
		return (
			<Panel id={this.props.id} separator={false}>
				{this.props.fetchedLocalChart && <PanelHeader>{this.props.fetchedLocalChart.date}</PanelHeader>}
				{/*fetchedUser &&
				<Group title="User Data Fetched with VK Bridge">
					<Cell
						before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200}/> : null}
						description={fetchedUser.city && fetchedUser.city.title ? fetchedUser.city.title : ''}
					>
						{`${fetchedUser.first_name} ${fetchedUser.last_name}`}
					</Cell>
				</Group>*/}

				{this.props.fetchedChart && <Chart
					title="Динамика заражений в России"
					data={this.props.fetchedChart}
				/>}

				{!this.props.userIsSubscribed && <Group className="SignGroup" separator="hide">
					<Div className="BtnContainer">
						<Button size="l" onClick={e => {
							bridge.send("VKWebAppAllowMessagesFromGroup", {group_id: 193696611})
							.then(data => {
								Api.post({method: 'sub/sign', body: this.props.fetchedUser, handler: (result) => {
									this.props.setUserIsSubscribed(true);
								}});
						  });
						}}>
							Получать статистику в личные сообщения
						</Button>
					</Div>
				</Group>}

				{this.props.fetchedLocalChart && <Chart
					title={"В " + this.props.fetchedLocalChart.region.name}
					headerAside={<Button onClick={() => {this.openRegionsList(0)}}>Другие регионы</Button>}
					data={this.props.fetchedLocalChart.series}
					blured={!this.props.userIsSubscribed}
				/>}

				{this.props.fetchedWorldChart && <Chart
					title="В мире"
					headerAside={<Button onClick={() => {this.openRegionsList(1)}} data-to="regions-list">Другие страны</Button>}
					data={this.props.fetchedWorldChart}
				/>}

			</Panel>
		)
	}
}

const mapStateToProps = (state, ownProps) => {
  return {
      //world: state.world
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
      onOpenRegionsList: (world) =>
        new Promise((resolve) =>
          openRegionsList(world).then((action) => {
            dispatch(action);
            resolve();
          })
        )
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Home);
