/**
 * Действия связанные с регионами
 */

/**
 * Выбор региона России
 * @param  {Object} region  объект с параметрами региона
 * @return {Promise}
 */
export const selectRegion = (region) => {
  return new Promise((resolve, reject) => {
      resolve({
        type: 'REGION_SELECT',
        region: region
      });
  });
};

/**
 * Открыть список регионов России иили других стран
 * @param  {Object} world  0 регионы России, 1 - другие страны
 * @return {Promise}
 */
export const openRegionsList = (world) => {
  return new Promise((resolve, reject) => {
      resolve({
        type: 'REGION_LIST',
        world: world
      });
  });
};
