/**
 * Методы для работы с регионами
 */

const api = {region: {
  /**
   * Список регионов России или стран
   * @param {Integer} world 0 - регионы России, 1 - страны
   * @param {Function} handler обработчик ответа в случае успеха
   */
  list: (world, handler) => {
    return api.request({method: 'region/index?world='+world, handler: handler});
  }
}};

export default api;
