import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS } from '@vkontakte/vkui';
import { Panel, PanelHeader, PanelHeaderButton, List, Cell, Group, ScreenSpinner, Counter, Div } from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

import Api from '../core/api/Api';

// Redux
import { connect } from 'react-redux';
import { selectRegion } from '../redux/actions/region';

import './RegionsList.css';

const osName = platform();

class RegionsList extends React.Component {

	constructor(props) {
    super(props);
    this.state = {isLoading: true};
  };

	// Компонент примонтирован
	componentDidMount() {
		Api.region.list(this.props.world, (regions) => {
      this.setState({
        isLoading: false,
        regions: regions,
      });
    });
  };

	// Выбор региона
  select = (region) => {
    this.props.onSelectRegion(region).then(() => this.props.navigate('region-chart'));
  };

	render() {
		return (
			<Panel id={this.props.id}>
				<PanelHeader
					left={<PanelHeaderButton onClick={() => {this.props.navigate('home')}}>
						{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
					</PanelHeaderButton>}
				>
					Статистика по регионам
				</PanelHeader>
				{!this.state.isLoading && <Group>
		      <List className="RegionsList">
						<Cell expandable>
							<Div></Div><Div>Заражений</Div>{/*<Div>Вылечено</Div><Div>Погибло</Div>*/}
						</Cell>
						{this.state.regions.map((region) => {
              return (
								<Cell
									key={"region" + region.rid}
									expandable
									onClick={() => {this.select(region)}}
								>
			        		<Div>
										{region.name}
									</Div>
									<Div>
										{region.confirmed}
										{region.confirmed_growth == 0 ? false : <Counter size="s">+{region.confirmed_growth}</Counter>}
									</Div>
									{/*<Div>
										{region.cured}
										{region.cured_growth == 0 ? false : <Counter size="s">+{region.cured_growth}</Counter>}
									</Div>
									<Div>
										{region.deaths}
										{region.deaths_growth == 0 ? false : <Counter size="s">+{region.deaths_growth}</Counter>}
									</Div>*/}
								</Cell>
							);
						})}
					</List>
				</Group>}
				{this.state.isLoading && <ScreenSpinner size='large' />}
			</Panel>
		);
	}
}

const mapStateToProps = (state, ownProps) => {
	console.log('RegionsList.mapStateToProps', state, ownProps);
  return {
      //region: state.region,
			world: state.region.world
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
      onSelectRegion: (region) =>
        new Promise((resolve) =>
          selectRegion(region).then((action) => {
            dispatch(action);
            resolve();
          })
        )
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(RegionsList);
