import React from 'react';
import { PanelHeader, Header, Div, Group, Button, Panel } from '@vkontakte/vkui';
import { BarChart, Bar, XAxis, YAxis, Tooltip, CartesianGrid, Legend, ReferenceLine } from 'recharts';

import './styles.css';

/**
 * [description]
 * @param  {String} title Заголовок
 */
export default ({title, headerAside, data, blured}) => (
  <Group header={
    <Header aside={headerAside}>{title}</Header>
  }>
    <BarChart
      width={window.innerWidth}
      height={300}
      data={data}
      margin={{top: 20, right: 30, left: 20, bottom: 5}}
      className={blured ? 'BluredChart' : ''}
    >
      <CartesianGrid strokeDasharray="3 3" />
      <XAxis dataKey="day" xAxisId={0} />
      <XAxis dataKey="day" xAxisId={1} hide />
      <XAxis dataKey="day" xAxisId={2} hide />
      <YAxis
        domain={[0, dataMax => (data[data.length-1].confirmed)]}
        tickFormatter={round}
      />
      <Tooltip />
      <Legend />
      <ReferenceLine
        y={data[data.length-1].confirmed}
        label={"Заражений на сегодня — "+parseInt(data[data.length-1].confirmed).toLocaleString()}
        stroke="#ccc"
      />
      <Bar dataKey="confirmed" name="Заражений" xAxisId={0} fill="#8884d8" />
      <Bar dataKey="cured" name="Вылечено" xAxisId={1} fill="#82ca9d" />
      <Bar dataKey="deaths" name="Погибло" xAxisId={2} fill="#777" />
    </BarChart>
  </Group>
);

const round = (val) => {
  if(val > 1000000) {
     return Math.round10(val / 1000000, -1) + " млн."
  } else if(val > 1000) {
     return Math.round10(val / 1000, -1) + " тыс."
  }
  return val;
}
