import React from 'react';
import { platform, IOS } from '@vkontakte/vkui';
import { Panel, PanelHeader, Header, PanelHeaderButton, List, Cell, Group, ScreenSpinner, Counter, Div } from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Chart from '../components/charts/Chart';

import Api from '../core/api/Api';

// Redux
import { connect } from 'react-redux';

const osName = platform();

class RegionChart extends React.Component {

	constructor(props) {
    super(props);
    this.state = {isLoading: true};
  };

	// Компонент примонтирован
	componentDidMount() {
		// График города/области
		Api.chartRegion(this.props.region.rid, (chartData) => {
      this.setState({
        isLoading: false,
        chartData: chartData
      });
    });
  }

	render() {
		return (
			<Panel id={this.props.id}>
				<PanelHeader
					left={<PanelHeaderButton onClick={() => {this.props.navigate('regions-list')}}>
						{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
					</PanelHeaderButton>}
				>
					Статистика
				</PanelHeader>
				{!this.state.isLoading && <Chart
					title={"Динамика заражений в " + this.state.chartData.region.name}
					data={this.state.chartData.series}
				/>}
				{this.state.isLoading && <ScreenSpinner size='large' />}
			</Panel>
		);
	}
}

const mapStateToProps = (state, ownProps) => {
  return {
      region: state.region.region
  };
};

export default connect(mapStateToProps)(RegionChart);
