/**
 * Редуктор регионов
 */

export default function reducer(state = null, action) {
  switch (action.type) {
    // Выбор региона
    case 'REGION_SELECT':
      return Object.assign({}, state, {region: action.region});

      // Открыть список регионов России иили других стран
      case 'REGION_LIST':
        return Object.assign({}, state, {world: action.world});

    default:
      return state;
    }
}
